				"html": "<ol>\n<li>Separate subject from body with a blank line</li>\n<li>Limit the subject line to 50 characters</li>\n<li>Capitalize the subject line</li>\n<li>Do not end the subject line with a period</li>\n<li>Use the imperative mood in the subject line</li>\n<li>Wrap the body at 72 characters</li>\n<li>Use the body to explain what and why vs. how</li>\n</ol>\n<p><strong>A properly formed git commit subject line should always be able to complete the following sentence:</strong></p>\n<blockquote>\n<p>If applied, this commit will <em><code>&#x3C;&#x3C;your subject line here>></code></em></p>\n</blockquote>\n<p><strong>For example:</strong></p>\n<ul>\n<li>If applied, this commit will <strong><em>Refactor subsystem X for readability</em></strong></li>\n<li>If applied, this commit will <strong><em>Update getting started documentation</em></strong></li>\n<li>If applied, this commit will <strong><em>Remove deprecated methods</em></strong></li>\n<li>If applied, this commit will <strong><em>Release version 1.0.0</em></strong></li>\n<li>If applied, this commit will <strong><em>Merge pull request <span class=\"hashtag\">#123</span> from user/branch</em></strong></li>\n</ul>\n<p><strong>Notice how this doesn't work for the other non-imperative forms:</strong></p>\n<ul>\n<li>If applied, this commit will <em><span class=\"bbcode-s\">fixed bug with Y</span></em></li>\n<li>If applied, this commit will <em><span class=\"bbcode-s\">changing behavior of X</span></em></li>\n<li>If applied, this commit will <em><span class=\"bbcode-s\">more fixes for broken stuff</span></em></li>\n<li>If applied, this commit will <em><span class=\"bbcode-s\">sweet new API methods</span></em></li>\n</ul>\n<p><strong>Remember:</strong> <em>Use of the imperative is important only in the subject line. You can relax this restriction when you're writing the body.</em></p>\n<p><strong>Reference:</strong> <a href=\"http://chris.beams.io/posts/git-commit\">http://chris.beams.io/posts/git-commit</a></p>",
